import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
// import { CusOperationHttpService } from './../../http/custom-operation/custom-operation.http.service';//我的客户群信息查询
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { SelectItem, Message, ConfirmationService, ConfirmDialogModule, AccordionModule } from 'primeng/primeng';
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';//我的客户群信息查询

import * as _ from 'lodash';

import { TZB_HTTP_XIN, TZB_HTTP_XIN_C } from "app/@uisftech/common/service/http.constant";
@Component({
  selector: 'app-customer-information',
  templateUrl: './customer-information.component.html',
  styleUrls: ['./customer-information.component.scss'],
  providers: [Commfunc, ConfirmationService]
})

export class CustomerInformationComponent implements OnInit {
  public show: number = 1;
  ps:any[];
  code:any [];
  groupInfos:any;//客群信息数组
  groupId:any;
  groupInfo:any;
  groupName:any;//客群名称
  groupTypes:any;//客群类型数组
  featuredProducts:any;//主推产品数组
  featuredProduct:any;//主推产品
  groupType:any;//客群类型
  groupOwner:any;//群主
  groupOfContacts:any;//群联系人
  orgId:any;//所属机构
  
  constructor(
    public ActivatedRoute: ActivatedRoute,
    // public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public httpService: CusOperationHttpService,
    public confirmationService: ConfirmationService,
    public router: Router,
    public route: ActivatedRoute,
    public CommonHttpService: CommonHttpService
  ) {
    // ;
  }
  ngOnInit() {
    this.codeValues();
    console.log(this.code)
    this.ps = [
      {label: 'Audi', value: 'Audi'},
      {label: 'BMW', value: 'BMW'},
      {label: 'Fiat', value: 'Fiat'}
    
    ]
//客群号回显
    if (this.route.snapshot.params) {
      this.groupInfo=this.route.snapshot.params
      this.groupId=this.groupInfo['groupId']
      console.log(this.groupId)



      let param={
  "pageNum": 1,
  "pageSize": 10,
  "conditionList": [
    [
      {
        "field": "group_id",
        "operator": "=",
        "value": this.groupInfo.groupId
      }
    ]
  ],
  "isAssist": null,
  "groupType": "0",
  "advanceInput": "0"
  };
  
      this.querykq(param);
     }
  
}

 //码值
 codeValues() {
  this.commfunc.codeValue();
  let ret = this.commfunc.codeDatas;
  return this.code = ret //code是自己声明的变量
}


// 客群信息回显
querykq(param){
  this.httpService.queryCustGroupInfo(param).subscribe(data => {
    console.log(data)
    this.groupInfos=data.resultList[0];
    console.log( this.groupInfos)
    this.groupName=this.groupInfos.group_name;
   
    this.groupTypes=this.code['GroupType'];
    // this.featuredProducts=this.code['productList'];
    this.groupType=this.groupInfos.group_type;
    // this.featuredProduct=this.groupInfos.featured_products;
    this.groupOwner=this.groupInfos.group_owner;
    this.groupOfContacts=this.groupInfos.group_of_contacts;
  
    this.orgId=this.groupInfos.org_id;
    
   
    

  })

}


//跳转客群修改界面
resetClick(){

  this.router.navigate(['/pages/tzb/custom/custom-group/custom-group-view;groupId=MTAwMjkwMDAwMwczOWE1MUY0ZkQ3M0ZiNTBl']);


}
}