import { Routes, RouterModule, ChildActivationEnd } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { BatchPreCreditAddComponent } from "./batch-precredit-add.component";
import { BasicInformationComponent} from "./basic-information/basic-information.component";
import {  FieldInversComponent } from "./field-invers/field-invers.component";
import { ApprpvalOpinionComponent} from "./apprpval-opinion/apprpval-opinion.component";

//客户群组
export const routes: Routes = [
      {
        path: '', component: BatchPreCreditAddComponent,

        children:[
          {path: '',redirectTo:'basic-info',pathMatch:'full'},
          {path: 'basic-info', component: BasicInformationComponent},
          {path: 'field-inves', component: FieldInversComponent},
          {path: 'apprpval-opinion', component: ApprpvalOpinionComponent}
          
        ]
       


        
      }
     
     
    

    ]

export const BatchPrecreditAddRouting: ModuleWithProviders = RouterModule.forChild(routes);
