import { Component, OnInit } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { SelectItem, Message, ConfirmationService, ConfirmDialogModule, AccordionModule } from 'primeng/primeng';
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';//我的客户群信息查询

import * as _ from 'lodash';

import { TZB_HTTP_XIN, TZB_HTTP_XIN_C } from "app/@uisftech/common/service/http.constant";
@Component({
  selector: 'app-detail-information',
  templateUrl: './detail-information.component.html',
  styleUrls: ['./detail-information.component.scss'],
  providers: [Commfunc, ConfirmationService]
})

export class DetailInformationComponent implements OnInit {
  public show: number = 1;
  psname: string;
  data: any[];
  groupId: any[];
  btn1: boolean = true;
  btn2: number = 0;
  btns: any[]
  ps: any[]
  isshow: boolean = false;
  isdetail: boolean = true;
  change: boolean = true;
  detail: boolean = true;
  datass: any;
  constructor(
    public ActivatedRoute: ActivatedRoute,
    public commfunc: Commfunc,
    public confirmationService: ConfirmationService,
    public httpService: CusOperationHttpService,
    public router: Router,
    public route: ActivatedRoute,
    public CommonHttpService: CommonHttpService
  ) {
    // ;
  }
  ngOnInit() {

                if (this.route.snapshot.params) {
                  this.groupId = this.route.snapshot.params['groupId']

                }



                this.datass = [
                  {
                    name: "张wu", systemedu: 100000, phone: 18530038464, age: 22, peopleedu: 50000, issave: true

                  },
                  {
                    name: "李wu", systemedu: 200000, phone: 13939839364, age: 44, peopleedu: 100000, issave: true
                  }
                ]
                this.ps = [
                  { label: 'Audi', value: 'Audi' },
                  { label: 'BMW', value: 'BMW' },
                  { label: 'Fiat', value: 'Fiat' }

                ]
                  let params = {
                    groupId: this.groupId,
                    listType: 1,
                    pageNum: 1,
                    statusId: "1",
                    pageSize: 10000
                  }


    this.queryDetail(params)

  }


  foujue() {

    this.isshow = true;
  }
  isdeta(i) {
    console.log(i)
    if (i == '1') {
      this.isdetail = true;
      this.change = true;
      this.detail = true;
    } else if (i == '2') {
      this.isdetail = false;
      this.change = false;
      this.detail = false;
    }
  }
  //明细信息列表
  queryDetail(param) {


    this.httpService.selectCustGroupList(param).subscribe(data => {

      if (data.returnCode.code = "success") {
        if (data.custList && data.custList.length > 0) {

          for (let i = 0; i < data.custList.length; i++) {
            data.custList[i]['issave'] = true;
          }
          this.data = data.custList;
        }

      }



    })





  }
  //切换修改和保存按钮
  detailupdate(item, i) {

    this.data[i].issave = !item['issave']

  }



}
